function randomPick(arr) {
  const selected = arr[Math.floor(Math.random() * arr.length)];

  return selected;
}

const images = [
  'images/svg/ava02.svg',
  'images/svg/ava03.svg',
  'images/svg/ava04.svg',
  'images/svg/ava05.svg',
  'images/svg/ava06.svg',
  'images/svg/ava07.svg',
  'images/svg/ava08.svg',
  'images/svg/ava09.svg',
  'images/svg/ava11.svg',
  'images/svg/ava12.svg',
  'images/svg/ava13.svg',
];

const txt = ['Hallo', 'Hi', '안녕하세요', 'Hello', 'Hola', 'Moin', 'Yoo'];

// function randomHover() {}

const test1 = document.getElementById('hover');

test1.addEventListener('mouseenter', event => {
  const myImage = document.getElementById('hover');
  const selImage = randomPick(images);
  const selText = randomPick(txt);

  document.getElementById('yoo').innerText = `${selText}!`;
  myImage.src = selImage;
});

const test2 = document.getElementById('hover');

test2.addEventListener('mouseleave', event => {
  const myImage = document.getElementById('hover');
  myImage.src = 'images/svg/ava01.svg';
  const selText = randomPick(txt);
  document.getElementById('yoo').innerText = `YTK ${selText}!`;
});

